#news {
  .news-block-black {
    background: #222222;

    .news-block {
      display: flex;

      .news-full {
        max-width: 50%;
        padding: 0 80px;
        margin: auto;

        .news-info {
          font-size: 13px;
          font-weight: 400;
        }

        .news-name {
          font-family: 'GothamProBold';
          font-size: 25px;
          font-weight: 500;
          text-transform: uppercase;
        }

        .news-text {
          font-size: 15px;
          font-weight: 400;
        }
      }

      .news-img {
        width: 50%;
      }
    }
  }

  .news-block-white {
    background: #f3f3f3;
    color: #222222;

    .news-block {
      display: flex;
      flex-direction: row-reverse;

      .news-full {
        max-width: 50%;
        padding: 0 80px;
        margin: auto;

        .news-info {
          font-size: 13px;
          font-weight: 400;
        }

        .news-name {
          font-family: 'GothamProBold';
          font-size: 25px;
          font-weight: 500;
          text-transform: uppercase;
        }

        .news-text {
          font-size: 15px;
          font-weight: 400;
        }
      }

      .news-img {
        width: 50%;
      }
    }
  }

  @media screen and (max-width: 991px) {
    .news-block-black {
      .news-block {
        .news-full {
          padding: 30px 15px 0 30px;
        }

        .news-img {
          margin: auto;
        }
      }
    }

    .news-block-white {
      .news-block {
        .news-full {
          padding: 30px 30px 0 15px;
        }

        .news-img {
          margin: auto;
        }
      }
    }
  }

  @media screen and (max-width: 767px) {
    .news-block-black {
      padding-top: 30px;

      .news-block {
        flex-direction: column;

        .news-full {
          max-width: 100%;
          padding: 0 30px;
        }
        .news-img {
          width: 100%;
        }
      }
    }

    .news-block-white {
      padding-top: 30px;

      .news-block {
        flex-direction: column;

        .news-full {
          max-width: 100%;
          padding: 0 30px;
        }
        .news-img {
          width: 100%;
        }
      }
    }
  }

  @media screen and (max-width: 575px) {
    .news-block-black {
      padding-top: 10px;

      .news-block {
        flex-direction: column;

        .news-full {
          max-width: 100%;
          padding: 0 10px;

          .news-name {
            font-size: 20px;
          }
        }
        .news-img {
          width: 100%;
        }
      }
    }

    .news-block-white {
      padding-top: 10px;

      .news-block {
        flex-direction: column;

        .news-full {
          max-width: 100%;
          padding: 0 10px;

          .news-name {
            font-size: 20px;
          }
        }
        .news-img {
          width: 100%;
        }
      }
    }
  }
}