#design {
  background: url("/img/design/bg-design.png") center / cover no-repeat;
  padding: 130px 0;

  .design-info {

    .design-name {
      font-family: 'GothamProMedium';
      font-size: 25px;
      font-weight: 500;
      text-transform: uppercase;
      margin-bottom: 30px;
    }

    .design-text {
      font-size: 15px;
      font-weight: 400;
      margin-bottom: 60px;
    }

    ul {
      margin: 0;
      padding: 0;

      li {
        list-style: none;

        a {
          font-family: 'GothamProBold';
          color: #ffffff;
          font-size: 15px;
          font-weight: 500;
          text-transform: uppercase;
          background-color: #11749e;
          display: inline-block;
          padding: 10px 0 10px 20px;
          margin: 5px 0;

          &:hover {
            text-decoration: none;
          }
        }

        &:nth-child(1) a {
          width: 370px;
        }

        &:nth-child(2) a {
          width: 400px;
        }

        &:nth-child(3) a {
          width: 430px;
        }
      }
    }
  }

  .col-6:nth-child(2) {
    text-align: center;
  }

  @media screen and (max-width: 991px) {
    .design-phone {
      display: none;
    }
  }

  @media screen and (max-width: 767px) {
    padding: 50px 0;

    .design-phone {
      display: none;
    }
  }

  @media screen and (max-width: 575px) {
    padding: 10px 0;

    .design-phone {
      display: none;
    }

    .design-info {
      ul {
        li {
          &:nth-child(1) a {
            width: 270px;
          }

          &:nth-child(2) a {
            width: 280px;
          }

          &:nth-child(3) a {
            width: 290px;
          }
        }
      }
    }
  }
}