#request {
  background:
          url("/img/request/bg-c.png") no-repeat 50% 9%,
          url("/img/request/bg-request.png") center / cover no-repeat;
  padding: 130px 0;

  form {
    input, textarea {
      background: none;
      color: #ffffff;
      border: 1px solid #ffffff;
      width: 100%;
      margin-bottom: 40px;
      padding: 10px;
    }

    .col:last-child {
      text-align: center;
    }

    .submit {
      font-family: 'GothamProBold';
      background: #11749e;
      width: 200px;
      font-size: 24px;
      font-weight: 500;
      text-transform: uppercase;
      padding: 10px 30px;
      margin: auto;
      border: none;
    }
  }

  @media screen and (max-width: 767px) {
    padding: 50px 0;
    background: url("/img/request/bg-request.png") no-repeat;

    h2 {
      margin-bottom: 30px;
    }

    form {
      input, textarea {
        margin-bottom: 20px;
      }
    }
  }

  @media screen and (max-width: 575px) {
    padding: 10px 0;
    background: url("/img/request/bg-request.png") no-repeat;

    h2 {
      font-size: 30px;
      margin-bottom: 10px;
    }

    form {
      input, textarea {
        margin-bottom: 10px;
      }
    }
  }
}