#team {
  color: #222222;
  padding: 130px 0;
  text-align: center;
  background: url("/img/team/bg-t.png") no-repeat 50% 8%;

  .team-desc {
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 70px;
  }

  .team-info {
    background-color: #ffffff;
    padding: 20px;
    margin: -55px auto 0 auto;
    z-index: 50;
    position: relative;
    width: 90%;

    .person-name {
      font-family: 'GothamProBold';
      color: #11749e;
      font-size: 20px;
      font-weight: 500;
      text-transform: uppercase;
      margin-bottom: 0;
    }

    .person-profession {
      font-family: 'GothamProMedium';
      font-size: 15px;
      font-weight: 500;
    }
  }

  @media screen and (max-width: 991px) {
    background: url("/img/team/bg-t.png") no-repeat 50% 5%;
  }

  @media screen and (max-width: 767px) {
    padding: 50px 0;
    background: none;

    h2 {
      margin-bottom: 30px;
    }

    .team-desc {
      margin-bottom: 30px;
    }
  }

  @media screen and (max-width: 575px) {
    padding: 10px 0;
    background: none;

    h2 {
      font-size: 30px;
      margin-bottom: 10px;
    }

    .team-desc {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }
}