#blog {
  color: #222222;
  text-align: center;
  background: url("/img/blog/bg-b.png") no-repeat 50% 8%;
  padding: 130px 0;

  .add {
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 76px;
  }

  .blog-name {
    font-family: 'GothamProBold';
    color: #11749e;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    margin-top: 40px;
  }

  .blog-desc {
    font-size: 15px;
    font-weight: 400;
  }

  @media screen and (max-width: 991px) {
    background: url("/img/blog/bg-b.png") no-repeat 50% 5%;
  }

  @media screen and (max-width: 767px) {
    background: none;
    padding: 50px 0;

    h2 {
      margin-bottom: 30px;
    }

    .add {
      margin-bottom: 30px;
    }
  }

  @media screen and (max-width: 575px) {
    background: none;
    padding: 10px 0;

    h2 {
      font-size: 30px;
      margin-bottom: 10px;
    }

    .add {
      font-size: 20px;
      margin-bottom: 10px;
    }

    .blog-name {
      font-size: 18px;
      margin-top: 20px;
      margin-bottom: 10px;
    }
  }
}