header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 20px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.38);

  .header {
    display: flex;
    align-items: center;

    #mobilemenu {
      font-family: 'MyriadPro';
      display: none;
    }

    #nav_menu {
      font-family: 'MyriadPro';
      margin-left: auto;
      margin-right: 50px;

      ul {
        display: flex;
        justify-content: space-between;
        padding-left: 0;
        margin-bottom: 0;

        li {
          list-style: none;

          a {
            color: #ffffff;
            font-size: 20px;
            font-weight: 400;

            &:hover, &:active, &.menu-item-active {
              text-decoration: none;
              padding-bottom: 40px;
              border-bottom: 6px solid $creatives-blue;
            }
          }

          &:not(:last-child) {
            margin-right: 40px;
          }
        }
      }
    }

    .social {
      a:not(:last-child) {
        margin-right: 8px;
      }
    }
  }

  @media screen and (max-width: 991px) {
    .header {
      justify-content: space-between;

      #nav_menu {
        display: none;
      }

      #mobilemenu {
        display: block;
      }
    }

    .slicknav_menu {
      background: none;
      padding: 0;
    }
  }
}
