#agency {
  text-align: center;
  background: $creatives-blue url("/img/bg-w.png") no-repeat 50% 15%;
  padding: 130px 0;
  min-height: 100vh;

  .nimbus {
    padding: 50px;
    border-radius: 50%;
    background-color: #2e85aa;
    max-width: 140px;
    max-height: 140px;
    margin: auto;
  }

  .property-name {
    font-family: 'GothamProBold';
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    margin: 25px 0 25px;
  }

  .property-desc {
    font-size: 15px;
    font-weight: 400;
  }

  @media screen and (max-width: 991px) {
    background: $creatives-blue url("/img/bg-w.png") no-repeat 50% 10%;
  }

  @media screen and (max-width: 767px) {
    padding: 50px 0;
    background: $creatives-blue;

    h2 {
      margin-bottom: 30px;
    }
  }

  @media screen and (max-width: 575px) {
    background: $creatives-blue;
    padding: 10px 0;

    h2 {
      font-size: 30px;
      margin-bottom: 10px;
    }
  }
}