#creatives {
  background: url("/img/creatives/bg-header.png") center / cover no-repeat;
  min-height: 100vh;
  text-align: center;
  padding-top: 116px;
  display: flex;
  align-items: center;

  h1 {
    font-family: 'GothamProBold';
    color: #f5f5f5;
    font-size: 153px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -1.92px;
  }

  .desc {
    font-family: 'GothamProBold';
    color: #11749e;
    font-size: 25px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 7.88px;
  }

  .scroll {
    font-family: 'Open Sans', sans-serif;
    animation: scroll linear 2s infinite alternate running;

    p {
      font-family: 'Open Sans', sans-serif;
      font-size: 13px;
      font-weight: 400;
    }
  }

  @-webkit-keyframes scroll {
    0% {
      transform: translateY(15px)
    }

    100% {
      transform: translateY(30px)
    }
  }

  @media screen and (max-width: 991px) {
    h1 {
      font-size: 140px;
    }
  }

  @media screen and (max-width: 767px) {
    h1 {
      font-size: 100px;
    }
  }

  @media screen and (max-width: 575px) {
    h1 {
      font-size: 60px;
    }

    .desc {
      font-size: 20px;
    }
  }
}