footer {
  background: #222222;
  padding: 50px 0;

  .copy {
    font-family: 'Roboto', sans-serif;
    display: flex;
    justify-content: space-between;

    p {
      margin-bottom: 0;
    }
  }

  @media screen and (max-width: 575px) {
    padding: 20px 0;

    .copy {
      flex-direction: column;
      text-align: center;
    }
  }
}