#info {
  background: $creatives-blue;
  padding: 130px 0;

  .info-desc {
    padding-left: 30px;
    margin-bottom: 0;
  }

  img {
    width: 70px;
    height: 70px;
    margin: auto;
  }

  @media screen and (max-width: 991px) {
    padding-top: 130px;
    padding-bottom: 80px;

    .info-desc {
      margin-bottom: 50px;
    }

    img {
      margin: 0;
    }
  }

  @media screen and (max-width: 767px) {
    padding-top: 50px;
    padding-bottom: 20px;

    .info-desc {
      margin-bottom: 30px;
    }

    img {
      margin: auto;
    }
  }

  @media screen and (max-width: 575px) {
    padding-top: 15px;

    .info-desc {
      padding-left: 15px;
      margin-bottom: 15px;
    }
  }
}